
#app {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  background: #e0e0e0;
}

.main-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: #fff;
  overflow: hidden;
}
.main-content .room-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  width: 100%;
}
.main-content .textarea-container {
  width: 100%;
  height: 20vw;
}
.main-content .textarea-container::before {
  display: block;
  content: " ";
  height: 1px;
  background: #e0e0e0;
}
.main-content .textarea-container textarea {
  width: 100%;
  height: 100%;
  padding: 1vw;
  font-size: 3.8vw;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: none;
  resize: none;
  outline: none;
}

.chat-room {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}
.chat-room .room-header {
  width: 100%;
}
.chat-room .room-header h3 {
  font-size: 2.4vw;
  margin: 0;
  line-height: 5vw;
}
.chat-room .room-content {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex: auto;
          flex: auto;
  width: 100%;
  padding: 0.5vw 1vw 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}
.chat-room .room-content .room-message-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  padding-top: 3vw;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
}
.chat-room .room-content .room-message-wrap .room-message-list {
  padding: 0;
  list-style: none;
}
.chat-room .room-content .room-message-wrap .room-message-list .room-message-list-item {
  margin-bottom: 10px;
}
.chat-room .room-content .room-message-wrap .load-tip {
  position: absolute;
  top: 1vw;
  left: 50%;
  margin-left: -10vw;
  display: inline-block;
  width: 20vw;
  text-align: center;
  line-height: 2vw;
  font-size: 1.5vw;
  color: #5252e4;
  cursor: pointer;
}

.message-box .message-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.message-box .message-body.reverse {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.message-box .message-body.reverse .messsage-username {
  text-align: right;
}
.message-box .message-body .message-sender {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 15vw;
}
.message-box .message-body .message-sender .potrait-img {
  width: 10vw;
  height: 10vw;
  border-radius: 2vw;
  border: 1px solid #f0f0f0;
  overflow: hidden;
}
.message-box .message-body .message-sender img {
  width: 100%;
  height: 100%;
}
.message-box .message-body .messsage-username {
  margin-bottom: 1vw;
  font-size: 1.6vw;
  line-height: 1;
  text-align: left;
}
.message-box .message-body .message-content {
  max-width: 40vw;
  min-height: 7.5vw;
  padding: 1.5vw;
  border-radius: 10px;
  background: #9cdd9c;
}
.message-box .message-body .message-content img {
  max-width: 25vw;
  max-height: 30vw;
}
.message-box .message-body .message-content p {
  margin: 0;
  line-height: 1.5;
  font-size: 5vw;
  text-align: left;
  word-break: break-all;
}
.message-box .message-body .message-sys {
  margin: 0 auto;
  font-size: 2vw;
  text-align: center;
}
.message-box .message-body .message-sys span {
  display: inline-block;
  padding: 1vw 2vw;
  line-height: 1.2;
  background: #f0f0f0;
  border-radius: 1vw;
}
